import React from 'react';
import { CModal, CModalBody, CModalHeader, CModalFooter, CButton, CModalTitle,CCloseButton } from '@coreui/react';
import Register from './Register';
import { useState } from 'react';
const RegisterModal = ({ visible, toggle }) => {
    const [formData, setFormData] = useState({
        username: '',
        email: '',
        password: '',
        confirmPassword: '',
        displayName: '',
        phoneNumber: '',
        birthday: '',
        region: '',
        identityCard: '',
        university: '',
        major: '',
        year: '',
        studentId: '',
        linkFb: ''
      });
    
      const handleChange = (e) => {
        const { id, value } = e.target;
        setFormData({
          ...formData,
          [id]: value
        });
      };
    
      const handleSubmit = (e) => {
        e.preventDefault();
        console.log('Form data:', formData);
      };
  return (
    <CModal
      alignment="center"
      visible={visible}
      onClose={toggle}
      aria-labelledby="VerticallyCenteredExample"
      size="lg"
      className="custom-modal"
    >
      <CModalBody className="text-white modal-body modal-content" >
        <div className="d-flex justify-content-end custom-close-button">
          <CCloseButton onClick={toggle} />
        </div>
        <h2 className="text-center" style={{marginBottom:"30px"}}>ng K</h2>
        <form id="signUpForm" onSubmit={handleSubmit} >
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="username">Tn ng nhp</label>
              <input
                type="text"
                className="form-control"
                id="username"
                placeholder="Tn ng nhp"
                value={formData.username}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                className="form-control"
                id="email"
                placeholder="Email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="password">Mt khu</label>
              <input
                type="password"
                className="form-control"
                id="password"
                placeholder="Mt khu"
                value={formData.password}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="confirmPassword">Nhp li mt khu</label>
              <input
                type="password"
                className="form-control"
                id="confirmPassword"
                placeholder="Nhp li mt khu"
                value={formData.confirmPassword}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="displayName">Tn hin th</label>
              <input
                type="text"
                className="form-control"
                id="displayName"
                placeholder="Tn hin th"
                value={formData.displayName}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="phoneNumber">S in thoi</label>
              <input
                type="text"
                className="form-control"
                id="phoneNumber"
                placeholder="S in thoi"
                value={formData.phoneNumber}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="birthday">Ngy sinh</label>
              <input
                type="date"
                className="form-control"
                id="birthday"
                value={formData.birthday}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="region">Khu vc</label>
              <input
                type="text"
                className="form-control"
                id="region"
                placeholder="Khu vc"
                value={formData.region}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="identityCard">Cn cc cng dn</label>
              <input
                type="text"
                className="form-control"
                id="identityCard"
                placeholder="Cn cc cng dn"
                value={formData.identityCard}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="university">Trng i hc</label>
              <input
                type="text"
                className="form-control"
                id="university"
                placeholder="Trng i hc"
                value={formData.university}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="major">Chuyn ngnh</label>
              <input
                type="text"
                className="form-control"
                id="major"
                placeholder="Chuyn ngnh"
                value={formData.major}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="year">Nm hc</label>
              <input
                type="number"
                className="form-control"
                id="year"
                placeholder="Nm hc"
                value={formData.year}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="studentId">M sinh vin</label>
              <input
                type="text"
                className="form-control"
                id="studentId"
                placeholder="M sinh vin"
                value={formData.studentId}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="linkFb">Link Facebook</label>
              <input
                type="text"
                className="form-control"
                id="linkFb"
                placeholder="Link Facebook"
                value={formData.linkFb}
                onChange={handleChange}
              />
            </div>
          </div>
          <div className="login-link-container">
            <span> c ti khon? <a href="/login">ng nhp</a></span>
          </div>
          <div className='custom-button-container'>
            <button type="submit" className="btn btn-light btn-block custom-register">ng k ngay</button>
          </div>

        </form>
      </CModalBody>
    </CModal>
  );
};

export default RegisterModal;
