import React, { useState } from 'react';

const Register = () => {
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    displayName: '',
    phoneNumber: '',
    birthday: '',
    region: '',
    identityCard: '',
    university: '',
    major: '',
    year: '',
    studentId: '',
    linkFb: ''
  });

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData({
      ...formData,
      [id]: value
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form data:', formData);
  };

  return (
    <div className="container" style={{maxWidth:"600px",marginTop: "50px"}}>
      <h2 className="text-center">ng K</h2>
      <form id="signUpForm" onSubmit={handleSubmit}>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="username">Tn ng nhp</label>
            <input
              type="text"
              className="form-control"
              id="username"
              placeholder="Tn ng nhp"
              value={formData.username}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              className="form-control"
              id="email"
              placeholder="Email"
              value={formData.email}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="password">Mt khu</label>
            <input
              type="password"
              className="form-control"
              id="password"
              placeholder="Mt khu"
              value={formData.password}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="confirmPassword">Nhp li mt khu</label>
            <input
              type="password"
              className="form-control"
              id="confirmPassword"
              placeholder="Nhp li mt khu"
              value={formData.confirmPassword}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="displayName">Tn hin th</label>
            <input
              type="text"
              className="form-control"
              id="displayName"
              placeholder="Tn hin th"
              value={formData.displayName}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="phoneNumber">S in thoi</label>
            <input
              type="text"
              className="form-control"
              id="phoneNumber"
              placeholder="S in thoi"
              value={formData.phoneNumber}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="birthday">Ngy sinh</label>
            <input
              type="date"
              className="form-control"
              id="birthday"
              value={formData.birthday}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="region">Khu vc</label>
            <input
              type="text"
              className="form-control"
              id="region"
              placeholder="Khu vc"
              value={formData.region}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="identityCard">Cn cc cng dn</label>
            <input
              type="text"
              className="form-control"
              id="identityCard"
              placeholder="Cn cc cng dn"
              value={formData.identityCard}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="university">Trng i hc</label>
            <input
              type="text"
              className="form-control"
              id="university"
              placeholder="Trng i hc"
              value={formData.university}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="major">Chuyn ngnh</label>
            <input
              type="text"
              className="form-control"
              id="major"
              placeholder="Chuyn ngnh"
              value={formData.major}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="year">Nm hc</label>
            <input
              type="number"
              className="form-control"
              id="year"
              placeholder="Nm hc"
              value={formData.year}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="studentId">M sinh vin</label>
            <input
              type="text"
              className="form-control"
              id="studentId"
              placeholder="M sinh vin"
              value={formData.studentId}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="linkFb">Link Facebook</label>
            <input
              type="text"
              className="form-control"
              id="linkFb"
              placeholder="Link Facebook"
              value={formData.linkFb}
              onChange={handleChange}
            />
          </div>
        </div>
        <button type="submit" className="btn btn-primary btn-block">ng k</button>
      </form>
    </div>
  );
};

export default Register;
